import './menu.css'
import Logo from './Startpage/Img/EyeCog_Logo.svg';
import { Link } from 'react-router-dom';

function Menu ({test}) {
    return (
      <div className='startmenu' id='top'>
          <Link to='/'>
          <div className="logo">
            <img src={Logo} alt="logo" id='logo' />
            <p className='color'>Async</p><p>hrone</p>
         </div>
         </Link>
        <div className="menu">
            <ul>
              <li><Link to="/">Home</Link></li>
              <li><Link to="/kontaktformular">Kontaktformular</Link></li>
              <li><Link to="/about">About</Link></li>
            </ul>
        </div>
        </div>
    );
}

export default Menu;