import './footer.css';
import React from 'react';
import Logo from '../Startpage/Img/EyeCog_Logo.svg';
import { Routes, Route, Link } from 'react-router-dom';
import Other from './Other';
import Startpage from './Startpage';
import AboutUs from './AboutUs';

const footer: React.FC = () => {
   return (
      <div className='start'>
        <footer>
        <div className="background">
         <div className="logo">
            <img src={Logo} alt="logo" />
            <p className='color'>Async</p><p>hrone</p>
         </div>
         <div className="footer">
            <div className="thispage">
                <ul>
                    <Routes>
                      <Route path='/' element={<Startpage />} />
                      <Route path='/about' element={<AboutUs />} />
                      <Route path='/*' element={<Other />} />
                    </Routes>
                </ul>
            </div>
            <div className="navfooter">
                <ul>
                    <li><a href="navigation">NAVIGATION</a></li>
                    <li><a href="#">Home</a></li>
                    <li><a href="#">Kontaktformular</a></li>
                    <li><a href="#">Kontakt</a></li>
                </ul>
            </div>
            <div className="general">
                <ul>
                    <li>ALLGEMEINES</li>
                    <li><Link to={"/impressum"}>Impressum</Link></li>
                    <li><Link to={"/datenschutzerklärung"}>Datenschutzerklärung</Link></li>
                </ul>
            </div>
         </div>
         </div>
         <div className="c"><p>2024 Asynchrone</p></div>
         </footer>
      </div>
   );
}

export default footer;