import { Routes, Route, useNavigate } from 'react-router-dom';
import React, { Suspense, lazy } from 'react';
const Startpage = lazy(() => import('./Startpage/startpage'));
const AboutUs = lazy(() => import('./AboutUs/AboutUs'));
const Form = lazy(() => import('./Form/Form'));
const Impressum = lazy(() => import('./Impressum'));
const Datenschutz = lazy(() => import('./Datenschutz'));

function Router () {
    const navigate = useNavigate();

   return (
    <>
    <Suspense>
    <Routes>
      <Route path='/' element={<Startpage />} />
      <Route path='/about' element={<AboutUs />} />
      <Route path='/kontaktformular' element={<Form navigate={navigate} />} />
      <Route path='/impressum' element={<Impressum />} />
      <Route path='/datenschutzerklärung' element={<Datenschutz />} />
    </Routes>
    </Suspense>
    </>
   );
}

export default Router;